@font-face {
  font-family: Proxima Nova;
  font-weight: 300;
  font-style: normal;
  src: url(../fonts/ProximaNova/ProximaNova-Light.otf); }

@font-face {
  font-family: Proxima Nova;
  font-weight: 400;
  font-style: normal;
  src: url(../fonts/ProximaNova/ProximaNova-Regular.otf); }

@font-face {
  font-family: Proxima Nova;
  font-weight: 600;
  font-style: normal;
  src: url(../fonts/ProximaNova/ProximaNova-Semibold.otf); }

@font-face {
  font-family: Proxima Nova;
  font-weight: 700;
  font-style: normal;
  src: url(../fonts/ProximaNova/ProximaNova-Bold.otf); }

body {
  margin: 0;
  color: #273758;
  font: 300 16px "Proxima Nova", sans-serif; }
  body * {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -ms-box-sizing: border-box;
    -o-box-sizing: border-box;
    box-sizing: border-box; }

img {
  display: block;
  width: 100%; }

h1 {
  line-height: 1.25em;
  font-weight: 300; }

h3 {
  line-height: 1.5em;
  font-weight: 300; }

p {
  line-height: 1.5em; }

.container {
  max-width: 960px;
  padding: 0.1px 20px;
  margin: 0 auto; }
  .container .row {
    margin-left: -10px;
    margin-right: -10px;
    margin-top: 1em;
    margin-bottom: 1em; }
    .container .row .col-1 {
      padding: 10px; }
    .container .row .col-2 {
      float: left;
      width: 50%;
      padding: 10px; }
      @media only screen and (max-width: 768px) {
        .container .row .col-2 {
          width: 100%; } }
    .container .row .col-3 {
      float: left;
      width: 33.33333%;
      padding: 10px; }
      @media only screen and (max-width: 768px) {
        .container .row .col-3 {
          width: 100%; } }
    .container .row .col-4 {
      float: left;
      width: 25%;
      padding: 10px; }
      @media only screen and (max-width: 768px) {
        .container .row .col-4 {
          width: 50%; } }

.btn, .btn-large {
  -webkit-appearance: none;
  height: 36px;
  margin: 0;
  padding: 10px 15px;
  font-family: "Proxima Nova", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #273759;
  background-color: transparent;
  border: solid thin #273759;
  cursor: pointer;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  border-radius: 5px; }
  .btn.rounded, .rounded.btn-large {
    -webkit-border-radius: 18px;
    -moz-border-radius: 18px;
    -ms-border-radius: 18px;
    -o-border-radius: 18px;
    border-radius: 18px; }

.btn-large {
  height: 48px;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: 300; }
  .btn-large.rounded {
    -webkit-border-radius: 24px;
    -moz-border-radius: 24px;
    -ms-border-radius: 24px;
    -o-border-radius: 24px;
    border-radius: 24px; }

.btn-color {
  color: #ffffff;
  background-color: #50d869;
  border: none; }

.list {
  padding-left: 15px; }
  .list.no-style {
    list-style: none;
    padding-left: 0; }
  .list li {
    padding: 0.25em 0; }

.bg-color {
  background-color: #f3f5f2 !important; }

.float-right {
  float: right !important; }

.float-left {
  float: left !important; }

.center {
  text-align: center; }

.clear {
  clear: both; }

.header {
  height: 90px;
  background-color: #f3f5f2; }
  .header .logo {
    height: 90px;
    width: 200px;
    background: transparent url(../images/logo.png) left center/120px auto no-repeat; }
  .header .nav-bar {
    list-style: none;
    margin: 0;
    padding: 0;
    line-height: 90px;
    color: #7d94b4; }
    @media only screen and (max-width: 768px) {
      .header .nav-bar {
        display: none; } }
    .header .nav-bar li {
      float: left;
      padding: 0 15px;
      cursor: pointer; }
  .header .login-button {
    margin-top: 27px; }
    @media only screen and (max-width: 768px) {
      .header .login-button {
        display: none; } }

@media only screen and (max-width: 768px) {
  .main .btn {
    display: block;
    margin: 0 auto; } }

.footer {
  color: #ffffff;
  background-color: #575757; }

